<template>
  <v-app ref="app">
    <v-app-bar
      app
      color="primary"
      dark
      v-show="showAppBar"
    >
      <v-app-bar-nav-icon @click="goHome">
        <v-icon>{{ mdiHome }}</v-icon>
      </v-app-bar-nav-icon>

      <v-toolbar-title>Origami Introduction</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon @click="$router.push('/query')">
        <v-icon>{{ mdiCalendarText }}</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main :style="{ padding: showAppBar ? '56px 0px 0px' : '0' }">
      <router-view :key="$route.fullPath" />
    </v-main>
  </v-app>
</template>

<script>
// import elementResizeDetectorMarker from 'element-resize-detector'
import { mdiHome, mdiCalendarText } from '@mdi/js';

export default {
  name: 'App',

  components: {
  },

  data: () => ({
    showAppBar: true,

    mdiHome,
    mdiCalendarText
  }),

  methods: {
    initListener () {
      // this.erd = elementResizeDetectorMarker()
      // this.erd.listenTo(this.$refs.app, () => {
      //   console.log(window.outerWidth, window.outerHeight)
      // })
      window.addEventListener('resize', this.resizeHandler)
      this.resizeHandler()
    },

    resizeHandler () {
      // console.log(window.outerWidth, window.outerHeight)
      this.showAppBar = !(window.outerWidth > window.outerHeight)
    },

    goHome () {
      this.$router.replace({
        path:'/empty',
        name:'Empty'
      })
    }
  },

  mounted () {
    this.initListener()
  }
};
</script>
