    height: 200px;
    border: 1px dashed lightgray;">
                  <div>
                    <v-icon x-large color="primary">{{ mdiCamera }}</v-icon>
                  </div>
                  <div>
                <span class="upload-caption">{{
                    hasImage1 ? "Replace" : "Click to upload"
                  }}</span>
                  </div>
                </div>
              </figure>

            </label>
          </image-uploader>

          <span>Upload your food / package above</span>

        </v-col>
      </v-row>

      <v-divider class="mt-2 mb-2"></v-divider>

      <v-row>
        <v-col cols="12">
          <div class="headline font-weight-bold">
            Detection Information
          </div>
        </v-col>

        <!--      <v-col cols="4">-->
        <!--        <v-subheader>Operator Name</v-subheader>-->
        <!--      </v-col>-->
