              hide-details="auto"
          ></v-text-field>
        </v-col>

        <!--      <v-col cols="4">-->
        <!--        <v-subheader>Detection Address</v-subheader>-->
        <!--      </v-col>-->
        <v-col cols="12">
          <v-text-field
