                  <div>
                    <v-icon x-large color="primary">{{ mdiCamera }}</v-icon>
                  </div>
                  <div>
                <span class="upload-caption">{{
                    hasImage2 ? "Replace" : "Click to upload"
                  }}</span>
                  </div>
                </div>
              </div>

            </label>
          </image-uploader>

          <span>Upload your origami as above</span>

        </v-col>

        <v-col cols="12" class="text-center">
          <v-btn color="primary" @click="nextStep">Submit</v-btn>
        </v-col>
      </v-row>

    </div>

    <div v-if="step === 5">
      <v-row>
        <v-col cols="12">
          Your photo has been uploaded successfully.
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <div class="d-flex" style="position: relative" id="copy-ticket" :data-clipboard-text="ticket">
            <v-img src="../assets/ticket.png">
            </v-img>
            <div id="copy-ticket-value" class="ticket"
                 style="position: absolute;left: 50%;top: 50%;transform: translate(-50%, -50%);color: #fff;font-size: 30px;font-weight: bold;">
              {{ ticket }}
            </div>
          </div>
        </v-col>
      </v-row>
