            assigned you a unique ticket, which you can use to query the results. Click the button (
            <v-icon color="primary">{{ mdiCalendarText }}</v-icon>
            ) in the upper right corner of our program to enter the query interface. Please take good care of your ticket.
            Click it to copy it to the clipboard.</p>
        </v-col>
      </v-row>
    </div>

    <v-dialog
        v-model="dialog"
        hide-overlay
        persistent
        width="300"
    >
      <v-card
          color="primary"
          dark
      >
        <v-card-text>
          Please stand by
          <v-progress-linear
              indeterminate
              color="white"
              class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>

    <div class="toast" v-show="toastShow">
      {{ toastText }}
    </div>

    <v-overlay :value="overlay">
      <v-progress-circular
          indeterminate
          size="64"
      ></v-progress-circular>
    </v-overlay>
