<script>
import { mdiCamera, mdiCalendarText } from "@mdi/js";
import ClipboardJS from 'clipboard'

export default {
  name: 'HelloWorld',

  data: () => ({
    apiKey: '657028a734eb4ce8bf3e0387df28f8eb',

    mdiCamera,
    mdiCalendarText,

    step: 1,
    dialog: false,
    toastShow: false,
    toastText: '',
    overlay: false,
    ticket: '',

    // Page II

    name: '',
    name_has_error: false,
    lon: 0,
    lat: 0,
    addr: '',
    date: (() => {
      const d = new Date()
      return d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
    })(),

    hasImage1: false,
    image1: null,

    // Page III

    steps: [
      {
        icon: 'step1',
        text: 'Add sample solution on page 1.',
      },
      {
        icon: 'step2',
        text: 'Fold page 1 to page 2 and incubate it for 30 min.',
      },
      {
        icon: 'step3',
        text: 'Fold page 2 to page 3，3 to 4 and incubate it.',
      },
      {
        icon: 'step4',
        text: 'Put the origami in the dark box and take photos.',
      },
    ],

    // Page IV

    hasImage2: false,
    image2: null,

  }),

  methods: {

    commit () {
      this.overlay = true
      let param = new FormData()
      param.append('name', this.name)
      param.append('file1', this.image1)
      param.append('file2', this.image2)
      param.append('lon', this.lon)
      param.append('lat', this.lat)
      param.append('intime', this.date)
      this.$post('/bpi/ticket', param)
          .then(data => {
            console.log(data)
            this.overlay = false
            this.step = 5
            this.ticket = data.data.data

            this.$nextTick(() => {
              const clipboard = new ClipboardJS('#copy-ticket')
              clipboard.on('success', (e) => {
                console.info('Action:', e.action)
                console.info('Text:', e.text)
                console.info('Trigger:', e.trigger)

                this.toast('Copied to clipboard', '')

                e.clearSelection()
              })

              clipboard.on('error', (e) => {
                console.error('Action:', e.action)
                console.error('Trigger:', e.trigger)

                this.toast('Copy failed', '')
              })
            })

          }).catch(e => {
            console.log(e)
            this.overlay = false
          })
    },

    nextStep () {
      // Before
      if (this.step === 2) {
        this.name_has_error = !this.$refs.name.validate()
        if (this.name_has_error) {
          return
        }
        if (!this.hasImage1) {
          this.toast('Upload your photo', '')
          return
        }
      } else if (this.step === 4) {
        if (!this.hasImage2) {
          this.toast('Upload your photo', '')
          return
        }
        this.commit()
        return
      }
      this.step++
      // End
      if (this.step === 2) {
        this.getLocation()
      }
    },

    toast (text, info) {
      let v = this
      v.toastText = text + ' ' + info
      v.toastShow = true
      setTimeout(function () {
        v.toastShow = false
      }, 1500)
    },

    // Page II

    setImage1 (output) {
      this.hasImage1 = true
      this.image1 = output
      console.log('setImage1: %o', output)
    },

    getLocationByGeo () {
      return new Promise((resolve) => {
        if ("geolocation" in navigator) {
          navigator.geolocation.getCurrentPosition(
            // Success callback function
            (position) => {
              // Get the user's latitude and longitude coordinates
              const latitude = position.coords.latitude
              const longitude = position.coords.longitude
              // console.log('geolocation: ', position.coords)
              resolve({ ok: 200, latitude, longitude })
            },
            // Error callback function
            (error) => {
              // Handle errors, e.g. user denied location sharing permissions
              resolve({ ok: 401, error })
            }
          )
        } else {
          resolve({ 
            ok: 402, 
            error: "Geolocation is not supported by the device."
          })
        }
      })
    },
    getLocationByIp () {
      return new Promise((resolve, reject) => {
        this.$ajax('get', `https://api.geoapify.com/v1/ipinfo?&apiKey=${this.apiKey}`)
          .then ( r => {
            const latitude = r.data.location.latitude
            const longitude = r.data.location.longitude
            resolve({ ok: 200, latitude, longitude })
          })
          .catch (error => {
            reject({ ok: 401, error })
          })
      })
    },
    parseLocation (lat, lon) {
      return new Promise((resolve, reject) => {
        this.$ajax('get', `https://api.geoapify.com/v1/geocode/reverse?lat=${lat}&lon=${lon}&apiKey=${this.apiKey}`)
          .then (r => {
            console.log('[parse] %o', r)
            resolve({
              ok: 200,
              // formatted: r.features[0].properties.formatted
              formatted: r.data.features[0].properties.address_line2
            })
          })
          .catch (error => {
            reject({ ok: 401, error })
          })
      })
    },

    getLocation () {
      this.dialog = true
      this.getLocationByGeo()
        .then(r => {
          console.log('geolocation: %o', r)
          if (r.ok !== 200) {
            return this.getLocationByIp()
          } 
          return r
        })
        .then(r => {
          this.lat = r.latitude
          this.lon = r.longitude
          return this.parseLocation(this.lat, this.lon)
        })
        .then(r => {
          this.addr = r.formatted
        })
        .catch(e => {
          console.log(e)
          this.toast('Fail to get your geolocation.', 'Sorry');
        })
        .finally(() => {
          this.dialog = false
        })
    },

    // Page IV

    setImage2 (output) {
      this.hasImage2 = true
      this.image2 = output
      console.log('setImage2: %o', output)
    },

  },

  mounted () {

  }
}
</script>

